<script>
export default {
  name: 'InsuranceCode',
  data() {
    return {
      fromReportView: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
  },
  methods: {
    goBack() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToMyPage() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    }
  }
}
</script>

